    const html = `
<h1 id="configuration">Configuration</h1>
<p>The main piece for running <code>pidoc</code> is the <em>docs.config.json</em> file. It should be placed in your main directory, where you'd run <code>piral-docs</code> (the command line utility for debugging and building the documentation).</p>
<h2 id="example-configuration">Example Configuration</h2>
<p>Below you'll find an example configuration.</p>
<pre><code class="hljs language-json">{
  <span class="hljs-attr">&quot;title&quot;</span>: <span class="hljs-string">&quot;Your title&quot;</span>,
  <span class="hljs-attr">&quot;description&quot;</span>: <span class="hljs-string">&quot;Description for metadata.&quot;</span>,
  <span class="hljs-attr">&quot;author&quot;</span>: <span class="hljs-string">&quot;Your name&quot;</span>,
  <span class="hljs-attr">&quot;branch&quot;</span>: <span class="hljs-string">&quot;main&quot;</span>,
  <span class="hljs-attr">&quot;repositoryUrl&quot;</span>: <span class="hljs-string">&quot;https://github.com/your-orga/your-repo&quot;</span>,
  <span class="hljs-attr">&quot;docsDirName&quot;</span>: <span class="hljs-string">&quot;docs&quot;</span>,
  <span class="hljs-attr">&quot;rootDir&quot;</span>: <span class="hljs-string">&quot;.&quot;</span>,
  <span class="hljs-attr">&quot;outputDir&quot;</span>: <span class="hljs-string">&quot;./dist&quot;</span>,
  <span class="hljs-attr">&quot;skipEditLabel&quot;</span>: <span class="hljs-literal">false</span>,
  <span class="hljs-attr">&quot;sitemap&quot;</span>: {
    <span class="hljs-attr">&quot;basics&quot;</span>: {
      <span class="hljs-attr">&quot;title&quot;</span>: <span class="hljs-string">&quot;Basics&quot;</span>,
      <span class="hljs-attr">&quot;sections&quot;</span>: [
        {
          <span class="hljs-attr">&quot;generator&quot;</span>: <span class="hljs-string">&quot;markdown&quot;</span>,
          <span class="hljs-attr">&quot;segment&quot;</span>: <span class="hljs-string">&quot;&quot;</span>,
          <span class="hljs-attr">&quot;dir&quot;</span>: <span class="hljs-string">&quot;basics&quot;</span>
        }
      ]
    }
  }
}
</code></pre>
<p>This configuration ends up with a single top-level section (called &quot;chapter&quot;) named &quot;basis&quot; (written on the page as &quot;Basics&quot;). It contains the content from a single section - everything in <code>docs/basics</code> will be consumed by the <code>markdown</code> generator. This is the most &quot;standard&quot; (or from a documentation-perspective &quot;expected&quot;) generator. It is capable of transforming Markdown files (<code>*.md</code>) to pages.</p>
<p>More about generators can be found in the <a href="/basics/01-overview">generators section</a>.</p>
<h2 id="configuration-typings">Configuration Typings</h2>
<p>(tbd)</p>
<h2 id="advanced-scenarios">Advanced Scenarios</h2>
<p>(tbd)</p>
`;
